.headerWrapper {
    height: 42px;
    background: #4a76a8;


}
.header{
    display:flex;
    justify-content:space-between;
    width:1310px;
    margin:  auto;
    padding: 10px 3px;


    color: white;
}

.headerBlock {
    display:flex;
    justify-content:space-between;
    vertical-align: middle;
    line-height: 13px;
    font-size: 13px;
    color: white;
}

.headerItem{

    vertical-align: middle;
    color: white;
    margin: auto 10px;
}
.headerItem:hover{
    color: #dbd7db;

}
.headerItem a {
    vertical-align: middle;
    line-height: 13px;
    font-size: 13px;
    color: white;
    cursor: pointer;
}
.headerItem a:hover{
    color: #dbd7db;
}