.users {
	height: 100%;
	display: flex;
	flex-direction: column;
	flex: 1;

	&__filter-panel {
		width: 100%;
		height: 80px;
		padding: 30px 40px 0px 40px;
		display: flex;
		justify-content: space-between;
		align-items: center;

		&__right__part {
			display: flex;
			align-items: center;

			.itemIcon {
				font-size: 1.2rem;
				color: #7a7a7a;
				padding: 0 6px;
                transition: .3s all;
                text-shadow: 1px 1px 2px black, 0 0 1em red;

              &:hover {
                cursor: pointer;
                transform: scale(1.2);

              }

			}

            .itemButton {
              font-size: 0.7rem;
              color: #7a7a7a;
              padding: 0 6px;
              transition: .3s all;




              button {

                padding: 0 6px;
                background-color: #2b87db;
                font-size:14px;
                text-transform:lowercase;
                height: 28px;
                width: 170px;
                border: none;
                color: #fff;
                outline: none;
                transition: .3s all;

                &:hover {
                  cursor: pointer;
                  background-color: #0050ec;
                }
              }
            }
		}
	}

	&__card-wrapper {
		width:100%;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		padding: 0px 40px 0px 40px;
		margin-bottom: 40px;
		animation: fadeIn .3s ease-in-out;
	}

	&__card {
		width: 100%;
		height: 259px;
		background-color: #fff;
		margin: 5px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		transition: .3s all;
		box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);


		&:hover {
			background-color: #f6fcff;
		}
		.card__data {
			display: flex;
			align-items: center;
			justify-content: normal;
			padding: 20px;
			color: #4e4e4e;

			.sliderContainer {
				width: 322px;
				height: 226px;
				//margin: 0px 50px;
				display:flex;
				//object-fit: cover;
				//position: relative;
				//height: 100vh;
				// width: 100vw;
				//margin: 0 auto;
				//overflow: hidden;
				//white-space: nowrap;

				.cardPhoto {
					width: 262px;
					height: 226px;
					object-fit: cover;



				}

				.left__slider {
					display: flex;
                    justify-content: center;
					position: relative;
                    height: 30px;
                    width: 30px;
					top: 45%;
					left: 35px;
					background: white;
					border-radius: 50%;
					cursor: pointer;
					align-items: center;
					transition: transform ease-in 0.1s;

					&:hover {
						transform: scale(1.1);
					}

					leftArrow {
						transform: translateX (-2px);

						&:focus {
							outline: 0;
						}
					}
				}

				.right__slider {
					display: flex;
					position: relative;
					right: 35px;
					top:45%;
					height: 30px;
					width: 30px;

					justify-content: center;
					background: white;
					border-radius: 50%;
					cursor: pointer;
					align-items: center;
					transition: transform ease-in 0.1s;

					&:hover {
						transform: scale(1.1);
					}

					rightArrow {
						transform: translateX(2px);

						&:focus {
							outline: 0;
						}
					}
				}

			}


			.advInfo {
				width:100%;
				margin: 20px;

				.infoSection {
					display: flex;
					justify-content: space-between;


					.infoSectionItem {
                        width:600px;
                        display: flex;
                        justify-content: space-between;
						font-size: 16px;
						font-weight: 700;
						margin-right: 20px;

						.pricePerSquare{
							font-weight: 500;
						}
					}

					.adv__settings {
						display:flex;

						.user__details {
							margin-right: 10px;
						}

						span {
							display: block;
							font-size: 1.3rem;
							color: #8c8c8c;

							&:hover {
								cursor: pointer;
								color: #3a3a3a;
							}
						}
					}

				}

				.advDescription {
					height: 60px;
					overflow: hidden
				}

				.panel__container {
					width: 100%;
					display: flex;

					button {
						background-color: #2b87db;
						height: 28px;
						border-radius: 14px;
						width: 185px;
						border: none;
						color: #fff;
						outline: none;
						transition: .3s all;
						text-transform: none;
					}

					.rightPanel {

					}

				}
			}

			/*&:hover {
                span {
                    display: block;
                }
            }*/


			h4 {
				margin-left: 15px;
				flex: 1;
			}


		}
	}
}