.item {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 30px 5px 5px 5px;

  .date {
    margin:5px;
    font-size: 14px;
    line-height: 1.43;
    color: #7a7a7a;
    text-decoration: none;
  }


  .field {
    display: flex;
    width: 100%;
    height: 100%;
    margin-top: 5px;
    transition: .3s all;

    .chart {
      width: 100%;
      height: 50%;
      background-color: #ffffff;
      padding: 30px;
      margin:5px;
      box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);

    }

    .content {
      display: flex;
      flex-direction: column;
      margin:5px;
      background-color: #ffffff;
      box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);


      .infoSection {
        display: flex;
        flex-direction: column;
        padding: 30px 40px 10px 40px;
        justify-content: space-between;


        .infoSectionItem {
          display: flex;
          font-size: 16px;
          font-weight: 700;
          margin-right: 20px;

          .pricePerSquare {
            font-weight: 500;
          }
        }

        .adv__settings {
          display: flex;

          .user__details {
            margin-right: 10px;
          }

          span {
            display: block;
            font-size: 1.3rem;
            color: #8c8c8c;

            &:hover {
              cursor: pointer;
              color: #3a3a3a;
            }
          }
        }

      }

      .sliderContainer {
        width: 850px;
        height: 460px;
        margin: 20px auto;
        // margin-left: 50px;
        //margin-top: 40px;
        display: flex;
        flex-direction: column;

        .sliderBox {
          display: block;
          position: relative;
          margin: 0 auto;
          width: 770px;
          height: 350px;
          border: 1px solid #dbd5da;
          overflow:hidden;

          &:hover {
            .left__slider{
              transform: translate(15px,-50%);
            }
            .right__slider {
              transform: translate(-15px, -50%);
            }
            .extender{
              transform: translate(-5px, 5px);
            }

          }

          .left__slider {
            display: block;
            position: absolute;
            top:50%;
            transform: translate(-50px,-50%);
            height: 22px;
            width: 14px;
            justify-content: center;
            background: white;
            border-radius: 50%;
            cursor: pointer;
            align-items: center;
            transition: transform ease-in 0.4s;
          }

          .right__slider {
            display: block;
            position: absolute;
            top:50%;
            right:0px;
            transform: translate(50px,-50%);
            height: 22px;
            width: 14px;
            justify-content: center;
            background: white;
            cursor: pointer;
            align-items: center;
            transition: transform ease-in 0.4s;

          }

          .extender{
            font-size:25px;
            display: block;
            position: absolute;
            top:0;
            right:0;
            transform: translate(100%,-100%);
            height: 30px;
            width: 30px;
            cursor: pointer;
            transition: transform ease-in 0.4s;
          }


          .mainPhoto {
            object-fit: cover;
            height: 100%;
            max-width: 100%;
            display: block;
            margin: 0 auto;


          }
        }


        .smallPhotos {
          width: 770px;
          height: 80px;
          display: flex;
          margin: 10px auto 0px auto;
          transition: all ease 1s;
          overflow: hidden;

          .activeSmallPhotoItems {
            border: 3px solid blue;
          }

          .smallPhotoItems {
            position: relative;
            margin-right: 10px;
            cursor: pointer;

            img {
              position: relative;
              width: 100px;
              height: 100%;
            }
          }

        }
      }


      .advInfo {
        padding: 10px 40px 30px 40px;
        text-align: justify;


        .advDescription {
          height: 60px;
          overflow: hidden
        }

        .panel__container {
          width: 100%;
          display: flex;

          button {
            background-color: #2b87db;
            height: 28px;
            border-radius: 14px;
            width: 185px;
            border: none;
            color: #fff;
            outline: none;
            transition: .3s all;
            text-transform: none;
          }

          .rightPanel {

          }

        }
      }

      .card__data {
        display: flex;
        align-items: center;
        justify-content: normal;
        padding: 20px;
        color: #4e4e4e;


        /*&:hover {
            span {
                display: block;
            }
        }*/


        h4 {
          margin-left: 15px;
          flex: 1;
        }


      }
    }
  }
}